	module.exports = {"data":{"post":{"id":"/Users/varya/WebDev/Personal/varya.github.com/content/life/life-in-finland-3/index_ru.md absPath of file >>> MarkdownRemark","html":"<h2>Образ жизни</h2>\n<p>Все говорят, что Финляндия жутко дорогая. А я думаю, что не очень-то, иногда даже дёшево.</p>\n<p>Но модель потребления тут совершенно другая. Если практиковать старые привычки, может действительно получиться\nневероятно\nдорого. Поэтому тут важно следовать местным лайфхакам и вообще понимать как система работает.</p>\n<excerpt/>\n<p>Возьмём, например, уборку дома. На первый взгляд кажется, что регулярно вызывать уборщика - для мажоров. Стоить это\nбудет для средней квартиры €60-80. Каждую неделю заказывать накладно. А потом оказывается, что на такие услуги есть\nналоговый вычет, причём аж 50% от их стоимости. И можно заранее оформить приблизительно, не надо ждать конца года. То\nесть тратишь на уборку €200 в месяц, но зато зарплату выдают на €100 больше. Чистый расход выходит всего €100 в месяц.</p>\n<p>В магазинах тоже регулярно скидки и акции. Есть календарь распродаж. Обязательно зимняя и летняя распродажи, в Стокманне\nещё весной и осенью Сумасшедшие дни. Скидки бывают и по 70%. И на Пасху бывают распродажи, даже в продуктовом.</p>\n<p>Спонтанные покупки, конечно, остаются дорогими. А обычная жизнь, если её организовывать так, что покупаешь с запасом,\nполучается намного дешевле. Такие вещи как средство для стирки или колготки я уже не помню когда без скидки покупала.\nДома у нас полно запасов таких расходников.</p>\n<p>Сейчас хочу сделать очки. Оправы стоят раза в 1,5 дороже, чем если их заказать из Англии или Германии. Но при этом если\nделаешь сразу линзы, скидка на оправу 70%. Это в нескольких оптиках так. Ещё в одной: если делаешь очки (которые не\nсильно дороже выходят, чем в Питере), то вторые такие же линзы в любую другую оправу - бесплатно.</p>\n<p>Перед очками проверяла зрение. Вообще можно по направлению бесплатно. Но наша медсестра в отпуске (июль же), а я хотела\nпоскорее. Пошла проверять за €20. В итоге денег вообще не взяли. Сказали, что у них сегодня акция и вообще им скучно\nбыло, а так они хоть развлеклись. И так во всем!</p>\n<h2>Дом</h2>\n<p><a href=\"https://fotki.yandex.ru/next/users/toivonens/album/168376/view/646061?page=0\" target=\"_blank\"><img src=\"https://img-fotki.yandex.ru/get/246987/14441195.52/0_9dbad_92c7f8ed_L.jpg\" width=\"500\" height=\"375\" border=\"0\" title=\"2017-08-05 20.41.14.jpg\" alt=\"2017-08-05 20.41.14.jpg\"></a></p>\n<p>Когда переезжаешь, надо уведомить управляющую домом компанию (информация обычно есть в самом подъезде на стенде), и они\nуже всем занимаются, дополнительно платить не нужно. Для новых жильцов на двери меняют буквы, напишут что скажешь,\nобычно принято писать фамилию одного или всех проживающих. И такие же изменения вносят в список на первом этаже.</p>\n<p>Во всех дверях есть слоты для посылок, размер стандартный по всей стране. И вроде бы в Швеции тоже точно такой же.\nНадпись тоже много где наборная, из таких же букв как на картинке. В таунхаусах и собственных домах некоторые пишут кто\nво что горазд. Один раз мы видели надпись, которую можно перевести примерно как “резиденция господина Виртанена и кошек\nМирри и Сельмы”.</p>\n<h2>Религия</h2>\n<p><a href=\"https://fotki.yandex.ru/next/users/toivonens/album/168376/view/646062?page=0\" target=\"_blank\"><img src=\"https://img-fotki.yandex.ru/get/467152/14441195.52/0_9dbae_f009a74c_L.jpg\" width=\"375\" height=\"500\" border=\"0\" title=\"2017-08-05 20.42.05.jpg\" alt=\"2017-08-05 20.42.05.jpg\"></a></p>\n<p>Архивное фото церкви из Тампере. Я здесь по церквям не ходок, знаю мало. Церкви в основном лютеранские, убранство\nсдержанное. Есть женщины, даже в чине епископов.  Прихожане платят налог (мы - нет).</p>\n<h2>Еда</h2>\n<p>Я попробовала приготовить marjapaistos — запеканку из ягод и овсяных хлопьев. Очень вкусно!</p>\n<p><a href=\"https://fotki.yandex.ru/next/users/toivonens/album/168376/view/646063?page=0\" target=\"_blank\"><img src=\"https://img-fotki.yandex.ru/get/249782/14441195.52/0_9dbaf_cbd28c31_L.jpg\" width=\"443\" height=\"500\" border=\"0\" title=\"2017-08-05 20.42.37.jpg\" alt=\"2017-08-05 20.42.37.jpg\"></a></p>\n<p>Впервые попробовала такую в Fazer Cafe, она же и на картинке. Это десерт, но не очень сладкий и немного кислый из-за\nягод. А овсяные хлопья создают ощущение здоровой еды (хаха!).</p>\n<p>Рецепт нашла на сайте Myllyn Paras. Вы кстати знали, что это финская фирма? Но хлопья я купила не их, а первые\nпопавшиеся нормальные, не быстрозавариваемые. В итоге — получилось. В следующий раз я бы положила поменьше мёда и слой\nовсяного теста сделала бы потоньше7 В кафе именно так, одни ягоды.</p>\n<p>Вот <a href=\"https://www.myllynparas.fi/reseptit/marjapaistos\">рецепт</a>!</p>\n<h2>В прошлых сериях</h2>\n<p>Интересно? Почитайте ещё предыдущие отрывки:</p>\n<ul>\n<li><a href=\"/ru/life/little-suomi-29-2017/\">Лето в Хельсинки - городские велики, черника и просто жизнь</a></li>\n<li><a href=\"/ru/life/little-suomi-27-2017/\">Обычная жизнь в Финляндии, 27 неделя 2017 года</a></li>\n<li><a href=\"/ru/life/little-things-in-finland/\">Финляндия: мелочи жизни</a></li>\n</ul>\n<p>Эти посты делаются по материалам моего Телеграм-канала <a href=\"https://t.me/suomi_ru\">@suomi_ru</a>.</p>","fields":{"slug":"ru/life/life-in-finland-3/","prefix":"","disqusIdentifier":"life-life-in-finland-3-index-ru"},"frontmatter":{"title":" Жизнь в Финляндии, отрывок 3","date":"06 August 2017","v2":true,"old":null,"tumblr":null,"meta":null,"cover":null}},"authornote":{"id":"/Users/varya/WebDev/Personal/varya.github.com/content/parts/author.md absPath of file >>> MarkdownRemark","html":"<p><strong>Mr. Gatsby</strong> Proin ornare ligula eu tellus tempus elementum. Aenean <a href=\"/\">bibendum</a> iaculis mi, nec blandit lacus interdum vitae. Vestibulum non nibh risus, a scelerisque purus. Blandit lacus interdum vitae. Vestibulum non nibh risus, a scelerisque purus.</p>"},"site":{"siteMetadata":{"siteUrl":"http://varya.me/"}}},"pathContext":{"slug":"ru/life/life-in-finland-3/","prev":{"id":"/Users/varya/WebDev/Personal/varya.github.com/content/life/life-in-finland-3/index_en.md absPath of file >>> MarkdownRemark","fields":{"slug":"en/life/life-in-finland-3/","prefix":"","lang":"en","disqusIdentifier":"life-life-in-finland-3-index-en"},"frontmatter":{"title":" Life in Finland, part 3"}},"next":{"id":"/Users/varya/WebDev/Personal/varya.github.com/content/life/life-in-finland-8/index_ru.md absPath of file >>> MarkdownRemark","fields":{"slug":"ru/life/life-in-finland-8/","prefix":"","lang":"ru","disqusIdentifier":"life-life-in-finland-8-index-ru"},"frontmatter":{"title":" Финляндия - общие душевые без занавесок и перегородок, булочки с корицей и парковочные привычки"}}}}

